body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.timer-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 100%;
  height: 100%; }

.timer-captures {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column; }

.current-timer {
  text-align: center;
  font-family: monospace;
  font-size: 65px;
  margin: auto;
  line-height: 3.3;
  width: 242px;
  height: 226px;
  border: 1px solid;
  border-radius: 100%;
  margin-bottom: 40px;
  margin-top: 40px; }

.timer-controls {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  margin-left: 20px; }

.timer-captures {
  text-align: center;
  margin-top: 20px; }

